.ribbonCor{
    margin-left: 450rem;

    }
    .box {
      position: relative;
      max-width: 600px;
      width: 500px;
      height: 400px;
      background: #fff;
      box-shadow: 0 0 15px rgba(0,0,0,.1);
    }
    
    /* common */
    .ribbon {
      width: 150px;
      height: 150px;
      overflow: hidden;
      position: absolute;
    }
    .ribbon::before,
    .ribbon::after {
      position: absolute;
      z-index: -1;
      content: '';
      display: block;
      border: 5px solid #ffc107;
    }
    .ribbon span {
      position: absolute;
      display: block;
      width: 205px;
      padding: 15px 0;
      background-color: #ffc107;
      box-shadow: 0 5px 10px rgba(0,0,0,.1);
      color: #fff;
      font: 700 18px/1 'Lato', sans-serif;
      text-shadow: 0 1px 1px rgba(0,0,0,.2);
      text-transform: uppercase;
      text-align: center;
    }
    
    /* top left*/
    .ribbon-top-left {
      top: -10px;
      left: -10px;
    }
    .ribbon-top-left::before,
    .ribbon-top-left::after {
      border-top-color: transparent;
      border-left-color: transparent;
    }
    .ribbon-top-left::before {
      top: 0;
      right: 0;
    }
    .ribbon-top-left::after {
      bottom: 0;
      left: 0;
    }
    .ribbon-top-left span {
      right: -25px;
      top: 30px;
      transform: rotate(-45deg);
    }
    
    /* top right*/
    .ribbon-top-rightx {
      top: 109px;
      right: -470px;
    }
    .ribbon-top-rightx::before,
    .ribbon-top-rightx.ribbon-bottom-rightx::before {
      border-top-color: transparent;
      border-right-color: transparent;
    }
    .ribbon-top-right::before {
      top: 0;
      left: 0;
    }
    .ribbon-top-rightx::after {
      bottom: 0;
      right: 0;
    }
    .ribbon-top-rightx span {
      left: -25px;
      top: 30px;
      transform: rotate(45deg);
    }
    
    /* bottom left*/
    .ribbon-bottom-left {
      bottom: -10px;
      left: -10px;
    }
    .ribbon-bottom-left::before,
    .ribbon-bottom-left::after {
      border-bottom-color: transparent;
      border-left-color: transparent;
    }
    .ribbon-bottom-left::before {
      bottom: 0;
      right: 0;
    }
    .ribbon-bottom-left::after {
      top: 0;
      left: 0;
    }
    .ribbon-bottom-left span {
      right: -25px;
      bottom: 30px;
      transform: rotate(225deg);
    }
  
    /* bottom right*/
    .ribbon-bottom-rightx {
      bottom: -10px;
      right: -10px;
    }
    .ribbon-bottom-rightx::before,
